import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map, skipWhile, switchMap, tap, shareReplay } from 'rxjs/operators';
import { Booking } from 'src/app/shared/models/Booking';
import { User } from 'src/app/shared/models/User';
import { BookingService } from 'src/app/shared/services/booking/booking.service';
import { AppService } from 'src/app/shared/services/client/app.service';
import { ScriptsService } from 'src/app/shared/services/client/scripts.service';
import { CompanyService } from 'src/app/shared/services/company/company.service';
import { JobService } from 'src/app/shared/services/recruitment/job.service';
import { UserService } from 'src/app/shared/services/user/user.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  user$: Observable<User> = this._us.user$;
  isAdmin: Observable<boolean> = this.user$.pipe(
    map((user:User)=>{
      return user.role.includes('Admin')
    })
  );
  successfulBookings:Observable<number> = this._bs.bookings$.pipe(
    map((bookings:Booking[])=>{
      return bookings.filter(b=>b.status=='successful').length;
    })
  )
  pendingBookings:Observable<number> = this._bs.bookings$.pipe(
    map((bookings:Booking[])=>{
      return bookings.filter(b=>b.status=='pending').length;
    })
  )
  spent:Observable<number> = this._bs.bookings$.pipe(
    map((bookings:Booking[])=>{
      // reduce((a, b) => +a + +b.price, 0)
      return bookings.filter(b=>b.status=='successful').reduce((a, b) => +a + +b.payment.amount, 0);
    })
  )

  app: Observable<any> = this.appService.onoingApp$;
  company$: Observable<any> = this.companyService.company$;
  jobPositions$: Observable<any> = this.company$.pipe(
    skipWhile(c => !c),
    switchMap((res:any)=>{
      return this.jobService.getJobPositionsApi({company:res.code}).pipe(
        tap((r:any)=>this.jobService.positions = r)
      )
    }),
    shareReplay()
  );
  jobOpenings$: Observable<any> = this.company$.pipe(
    skipWhile(c => !c),
    switchMap((res:any)=>{
      return this.jobService.getJobOpeningsApiSort({company:res.code}).pipe(
        tap((r:any)=>this.jobService.openings = r)
      )
    }),
    shareReplay()
  );
  jobApplications$: Observable<any> = this.company$.pipe(
    skipWhile(c => !c),
    switchMap((res:any)=>{
      return this.jobService.getJobApplicationApiSort({company:res.code}).pipe(
        tap((r:any)=>this.jobService.apps = r)
      )
    }),
    shareReplay()
  );
  noOfJobPositions$: Observable<any> = this.jobService.jobPositions$.pipe( map(r=>r.length));
  noOfJobOpenings$: Observable<any> = this.jobService.jobOpenings$.pipe( map(r=>r.length));
  noOfJobApplications$: Observable<any> = this.jobService.applications$.pipe( map(r=>r.length));

  constructor(private _us: UserService,
              private _bs: BookingService,
              private jobService: JobService,
              private companyService: CompanyService,
              private router: Router,
              private scriptService: ScriptsService,
              public appService: AppService) { }

  ngOnInit(): void {
    // this.app.subscribe();

  }
  navigateTo(url:any){
    this.router.navigate([url]).finally(
      ()=>{
        this.scriptService.scrollTo();
      }
    );
  }
}

