import { Component, OnInit } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, switchMap, tap, map } from 'rxjs/operators';
import { AppService } from 'src/app/shared/services/client/app.service';
import { DeviceService } from 'src/app/shared/services/client/device.service';
import { FileService } from 'src/app/shared/services/client/file.service';
import { ScriptsService } from 'src/app/shared/services/client/scripts.service';
import { CompanyService } from 'src/app/shared/services/company/company.service';
import { UserService } from 'src/app/shared/services/user/user.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styleUrls: ['./setting.component.scss']
})
export class SettingComponent implements OnInit {
  $user:Observable<any> = this._us.user$;
  changingPassword:boolean = false;
  currentPage:string = 'timelineAccount';
  newCompany:any = {
    name: '',
    website: '',
    logo: '',
    country: '',
    address: ''
  }
  passageAuth: string = environment.passageAppId;
  company$:Observable<any> = this._company.company$;
  constructor(private _us: UserService,private _fs: FileService, private _company: CompanyService,
    private _aps: AppService, private ss: ScriptsService, private _ds: DeviceService) { }

  ngOnInit(): void {
    this.company$.subscribe(
      c=>this.newCompany = c
    )
  }

  submit(){

  }
  showmodal(){
    const showCreateCompanyModal:any = document.getElementById('showCreateCompanyModal');
      showCreateCompanyModal.click();
  }
  handleFileInput(event: any) {
    const files: FileList = event.target.files;
    const fileToUpload:any = files.item(0);
    let formData = new FormData();
    formData.append('file', fileToUpload, fileToUpload.name)
    this._fs.upload(formData)
      .pipe(
        catchError((err: any) => {

          return throwError(err);
        })
      )
      .subscribe(
        f => {
          this.newCompany.logo = f.file;
        }
      )

  }
  save(event:any){

    event.preventDefault();
    if(!this.newCompany.name){
      this._ds.openErrorNotification('Name', 'Company name is required');
      return;
    }
    // if(!this.newCompany.website){

    // }
    if(!this.newCompany.logo){
      this._ds.openErrorNotification('Logo', 'Company logo is required');
      return;
    }
    if(!this.newCompany.country){
      this._ds.openErrorNotification('Country', 'Company country is required');
      return;
    }
    if(!this.newCompany.address){
      this._ds.openErrorNotification('Address', 'Company address is required');
      return;
    }
    let now = new Date(Date.now());
    this._company.update({
      ...this.newCompany,
      updated_at: now
    })
    .pipe(
      catchError(e=>{
        this.handleError(e);
        return throwError(e);
      })
    )
    .subscribe(
      c=>{

        this._ds.oSuccessNotification('Company Information Updated', `${this.newCompany.name} was successfully updated.`);

      }
    );

  }
  handleError(e:any){

    this._ds.oErrorNotification('opps', 'Issues while updating');

  }
  closeModal(){
    const selectCompany_header:HTMLSelectElement = <HTMLSelectElement>document.getElementById('selectCompany_header');
    if(selectCompany_header){
      selectCompany_header.options.selectedIndex = 0;
    }
  }
  handleSelectChange(e: any, clickedIndex: any, isSelected: any, previousValue: any) {


  }

}
